import moment from 'moment';
import React from 'react';
import { edit } from '../../../../assets/images/icons';
import { Checkbox } from '../../../common';
import cls from './TaskCard.module.scss';

function TaskCard(props) {
	const { task, openModal=()=>{}, setTaskToEdit=()=>{}, clicked=()=>{}, isEditable, priority } = props;
	const { name, process, status, add_info } = task;

	return (
		<div className={cls['task-card']}>
			{!task.completed_at ? (
				<div className={cls['task-card-container']}>
					{!process && <Checkbox label={`${priority !== undefined ? `${priority}` : ''} ${name} ${status !== null ? `  (${status})` : ''}`} onChange={clicked} />}
					{process && <p className={cls.clickLabel} onClick={clicked}>{` ${priority !== undefined ? `${priority}` : ''} ${name} ${status !== null ? `(${status})` : ''}`}</p>}
					{isEditable && (
						<img
							alt=""
							style={{ cursor: 'pointer' }}
							src={edit}
							onClick={() => {
								openModal();
								setTaskToEdit();
							}}
						/>
					)}
				</div>
			) : (
				<div
					className={cls['task-card-container']}
					style={{ flex: 1 }}
					onClick={() => {
						openModal();
						setTaskToEdit();
					}}
				>
					<p className={cls.clickLabel}>{`${priority !== undefined ? `${priority}` : ''}  ${name}`}</p>
					<p className={cls.clickLabel}>{moment(task.completed_at).format('DD of MMMM YYYY')}</p>
				</div>
			)}
		</div>
	);
}

export default TaskCard;
