import { Fragment, useState, useEffect } from 'react';
import 'react-tabs/style/react-tabs.css';
import { successLarge } from '../../../assets/images/new-icons';
import { Button, ModalR } from '../../common';
import HeaderTopBar from '../../common/HeaderTopBar/HeaderTopBar';
import cls from './CancelMembership.module.scss';

function CancelMemberShip(props) {
	const { onCancelMembership, onClose } = props;
	const [showQuestionaire, setShowQuestionaire] = useState(false);
	const [questionaireArray, setQuestionaireArray] = useState([]);
	const [questionIndex, setQuestionIndex] = useState(0);
	const [benefitsArray, setBenefitsArray] = useState([]);
	const [giveThanks, setGiveThanks] = useState(false);
	useEffect(() => {
		setQuestionaireArray([
			{ id: 0, question: 'Why are you cancelling your subscription?', answer: '' },
			{ id: 1, question: 'What would have made you keep using FlowHero?', answer: '' },
			{ id: 2, question: 'Would you ever reconsider using FlowHero in the future?', answer: '' },
		]);

		setBenefitsArray([
			{
				title: 'Real-Time Company-Wide Process Updates:',
				description: 'The moment you change a process in FlowHero, it immediately and automatically updates for everyone. No more need for extra notifications, training, etc. The next time someone follows that process, the latest version is always there.',
			},
			{
				title: "An Org Chart That's Actually Useful:",
				description:
					"No more static drawings. FlowHero is helping teams around the world understand exactly where they are in the org, who they're reporting to, and how the company operates. It's also helping managers share specific processes and information only with specific roles.",
			},
			{
				title: 'Full Transparency Of Every Moving Piece:',
				description: "FlowHero's Components system is empowering thousands of professionals to spend way less time updating spreadsheets and way more time doing the actual work they want to do! Everything everyone does in the company is automatically saved and timestamped.",
			},
			{ title: 'And A Dozen Other Benefits', description: 'Such as automatic issue/delay escalations up the management chain, the ability to create Procedure Flows to see how specific people or deliverables are flowing through your processes, and so much more.' },
		]);
	}, []);

	return (
		<Fragment>
			{!showQuestionaire ? (
				<div className={cls.membershipHost}>
					Cancel Your Subscription
					<p className={cls.cancelDescription}>
						Are you sure you want to cancel your subscription?
						<br />
						You will lose the following benefits:
					</p>
					{benefitsArray.map((benefit, index) => (
						<div key={index} className={cls.benefitContainer}>
							<p className={cls.benefitTitle}>{benefit.title}</p>
							<p className={cls.benefitDescription}>{benefit.description}</p>
						</div>
					))}
					<div className={cls.buttonsContainer}>
						<Button
							className={cls.unsubscribeButton}
							clicked={() => {
								//onCancelMembership();
								setShowQuestionaire(true);
							}}
							title={'Cancel Subscription'}
						/>
					</div>
					<div className={cls.buttonsContainer}>
						<Button
							className={cls.dontContinueButton}
							clicked={() => {
								onClose();
							}}
							title={"Don't Cancel Subscription"}
						/>
					</div>
				</div>
			) : (
				<div>
					<div className={cls.membershipHost}>
						A Few Questions Before You Go
						<p className={cls.cancelDescription}>{questionaireArray[questionIndex].question}</p>
					</div>
					<div className={cls.questionaireContainer}>
						<textarea
							type="text"
							name="textarea"
							rows={3}
							className={'textAreaStyle_T'}
							value={questionaireArray[questionIndex].answer ?? ''}
							onChange={e => {
								setQuestionaireArray(questionaireArray.map(q => (q.id === questionIndex ? { ...questionaireArray[questionIndex], answer: e.target.value } : q)));
							}}
						/>
					</div>

					<br />
					<div className={cls.membershipHost}>
						<div className={cls.buttonsContainer}>
							<Button
								className={cls.continueUnsubscribeButton}
								clicked={() => {
									if (questionIndex + 1 === questionaireArray.length) {
										setGiveThanks(true);
									} else {
										setQuestionIndex(questionIndex + 1);
									}
								}}
								title={'Continue'}
							/>
						</div>
						<span className={cls.cancelDescription}>{`Question ${questionIndex + 1} of ${questionaireArray.length}`}</span>
					</div>
				</div>
			)}
			<ThankyouModal giveThanks={giveThanks} onClose={() => onClose()} onCancelMembership={() => onCancelMembership({ reasons: questionaireArray })} />
		</Fragment>
	);
}

export default CancelMemberShip;

const ThankyouModal = props => {
	const { giveThanks, onClose, onCancelMembership } = props;

	return (
		<ModalR isOpen={giveThanks} shadowedContainerStyle={{ width: '30%', minWidth: '400px', paddingTop: '40px', paddingBottom: '40px' }} onClose={() => onClose()} scrollable={true} blured={true}>
			<div
				style={{
					display: 'flex',
					flexDirection: 'column',
					alignItems: 'center',
				}}
			>
				<img alt={''} src={successLarge} />
				<p
					style={{
						marginTop: '10px',
						fontFamily: 'var(--app-text-main-font)',
						fontSize: '24px',
						fontWeight: '600',
						color: 'blue',
						marginBottom: '5px',
						textAlign: 'center',
					}}
				>
					{'Thank You For Answering'}
					<br />
					{'The Questionaire!'}
				</p>

				<Button className={cls.dontContinueButton} style={{ width: '100px' }} clicked={() => onCancelMembership()} title={'Close'} />
			</div>
		</ModalR>
	);
};
